/* CSS for the squares */
.square {
  background: white;
  border: 1px solid gray;
  float: left;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  width: 80px;
  height: 80px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  line-height: 80px;
}

.square:focus {
  outline: none;
}

/* CSS for the board */
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

/* CSS for the game position itself */
.game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  margin: 0 auto;
}

/* CSS for the history buttons on the side */
ul {
  padding-left: 30px;
}
.game-info {
  margin-left: 20px;
}
.status {
  margin-bottom: 10px;
}
